// pages/dashboard.js
import { useState, useEffect } from "react";
import { useRouter } from "next/router";
import Layout from "../components/Layout";
import theme from "../theme";

import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

import {
  FaPills,
  FaCashRegister,
  FaChartLine,
  FaMoneyBillWave,
  FaUsers,
  FaClock,
} from "react-icons/fa";

export default function Dashboard() {
  const [user] = useState({ name: "ÿßŸÑŸÖÿØŸäÿ± ÿ£ÿ≠ŸÖÿØ", role: "admin" });
  const [users, setUsers] = useState([]);
  const [salesData, setSalesData] = useState([]);
  const router = useRouter();

  useEffect(() => {
    setUsers([
      { id: 1, name: "ŸÖÿ≠ŸÖÿØ ÿßŸÑÿµŸäÿØŸÑŸä", role: "pharmacist" },
      { id: 2, name: "ÿ£ÿ≠ŸÖÿØ ÿßŸÑŸÉÿßÿ¥Ÿäÿ±", role: "cashier" },
      { id: 3, name: "ŸÖŸáÿß ÿßŸÑÿ•ÿØÿßÿ±Ÿäÿ©", role: "admin" },
    ]);

    setSalesData([
      { month: "ŸäŸÜÿßŸäÿ±", total: 3200 },
      { month: "ŸÅÿ®ÿ±ÿßŸäÿ±", total: 4100 },
      { month: "ŸÖÿßÿ±ÿ≥", total: 3800 },
      { month: "ÿ£ÿ®ÿ±ŸäŸÑ", total: 5200 },
      { month: "ŸÖÿßŸäŸà", total: 6100 },
      { month: "ŸäŸàŸÜŸäŸà", total: 5700 },
    ]);
  }, []);

  const totalSales = salesData.reduce((s, m) => s + m.total, 0);

  // ÿ±Ÿàÿßÿ®ÿ∑ ÿßŸÑŸàÿµŸàŸÑ ÿßŸÑÿ≥ÿ±Ÿäÿπ ‚Äî ŸÖÿ≠ÿ≥ŸëŸÜÿ© ÿ®ÿµÿ±ŸäÿßŸã
  const quickLinks = [
    {
      title: "ÿßŸÑŸÖŸÜÿ™ÿ¨ÿßÿ™",
      icon: <FaPills />,
      path: "/products",
      color: "from-green-500 to-emerald-600",
    },
    {
      title: "ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™",
      icon: <FaCashRegister />,
      path: "/sales",
      color: "from-sky-500 to-blue-600",
    },
    {
      title: "ÿßŸÑÿ™ŸÇÿßÿ±Ÿäÿ±",
      icon: <FaChartLine />,
      path: "/reports",
      color: "from-purple-500 to-indigo-600",
    },
    {
      title: "ÿßŸÑÿ≠ÿ≥ÿßÿ®ÿßÿ™",
      icon: <FaMoneyBillWave />,
      path: "/accounts",
      color: "from-amber-500 to-yellow-600",
    },
    {
      title: "ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸàŸÜ",
      icon: <FaUsers />,
      path: "/users",
      color: "from-teal-500 to-cyan-600",
    },
    {
      title: "ÿßŸÑÿ¥ŸÅÿ™",
      icon: <FaClock />,
      path: "/shifts",
      color: "from-pink-500 to-rose-600",
    },
  ];

  return (
    <Layout user={user} title="ŸÑŸàÿ≠ÿ© ÿßŸÑÿ™ÿ≠ŸÉŸÖ">
      <div dir="rtl" className="space-y-10">

        {/* üîµ ÿßŸÑŸàÿµŸàŸÑ ÿßŸÑÿ≥ÿ±Ÿäÿπ ‚Äî ÿ™ÿµŸÖŸäŸÖ ÿßÿ≠ÿ™ÿ±ÿßŸÅŸä */}
        <section>
          <h2 className="mb-4 text-2xl font-bold text-gray-800">ÿßŸÑŸàÿµŸàŸÑ ÿßŸÑÿ≥ÿ±Ÿäÿπ</h2>

          <div className="grid grid-cols-1 gap-6 sm:grid-cols-2 lg:grid-cols-3 xl:grid-cols-6">
            {quickLinks.map((link, index) => (
              <button
                key={index}
                onClick={() => router.push(link.path)}
                className={`
                  relative flex flex-col items-center justify-center p-5 
                  rounded-2xl shadow-md bg-gradient-to-br ${link.color}
                  text-white transition-all duration-200 
                  hover:scale-[1.05] hover:shadow-xl
                `}
              >
                <div className="absolute inset-0 transition bg-black/10 rounded-2xl group-hover:bg-black/20"></div>
                <div className="relative mb-2 text-4xl">{link.icon}</div>
                <h3 className="relative text-sm font-semibold">{link.title}</h3>
              </button>
            ))}
          </div>
        </section>

        {/* üü¢ ÿ®ÿ∑ÿßŸÇÿßÿ™ ŸÖŸÑÿÆÿµ ‚Äî ÿ™ÿµŸÖŸäŸÖ ÿ£ŸÉÿ´ÿ± ŸÅÿÆÿßŸÖÿ© */}
        <div className="grid grid-cols-1 gap-4 sm:grid-cols-2 lg:grid-cols-4">
          <SummaryCard
            title="ÿ•ÿ¨ŸÖÿßŸÑŸä ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™"
            value={`${totalSales.toLocaleString()} ÿ±.ÿ≥`}
            color="text-sky-600"
          />
          <SummaryCard title="ÿπÿØÿØ ÿßŸÑŸÅŸàÿßÿ™Ÿäÿ±" value="248" color="text-blue-600" />
          <SummaryCard title="ÿπÿØÿØ ÿßŸÑÿ£ÿØŸàŸäÿ©" value="126" color="text-green-600" />
          <SummaryCard
            title="ÿπÿØÿØ ÿßŸÑŸÖÿ≥ÿ™ÿÆÿØŸÖŸäŸÜ"
            value={users.length}
            color="text-amber-600"
          />
        </div>

        {/* üìà ÿßŸÑÿ±ÿ≥ŸÖ ÿßŸÑÿ®ŸäÿßŸÜŸä ‚Äî ÿßÿ≠ÿ™ÿ±ÿßŸÅŸä */}
        <div className="p-5 bg-white border shadow-lg rounded-xl">
          <h3 className="mb-3 text-lg font-bold text-gray-800">
            ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™ ÿßŸÑÿ¥Ÿáÿ±Ÿäÿ©
          </h3>

          <ResponsiveContainer width="100%" height={260}>
            <LineChart data={salesData}>
              <CartesianGrid strokeDasharray="4 4" stroke="#e5e7eb" />
              <XAxis dataKey="month" stroke="#6b7280" />
              <YAxis stroke="#6b7280" />
              <Tooltip />
              <Line
                type="monotone"
                dataKey="total"
                stroke={theme.colors.primary}
                strokeWidth={3}
                dot={{ r: 5 }}
              />
            </LineChart>
          </ResponsiveContainer>
        </div>

        {/* üìù ÿ¢ÿÆÿ± ÿßŸÑÿπŸÖŸÑŸäÿßÿ™ + ÿßŸÑÿ™ÿ≠ŸÑŸäŸÑ */}
        <div className="grid grid-cols-1 gap-6 lg:grid-cols-3">
          
          <div className="p-5 bg-white border shadow-lg rounded-xl lg:col-span-2">
            <h3 className="mb-4 text-lg font-bold text-gray-800">ÿ¢ÿÆÿ± ÿßŸÑÿπŸÖŸÑŸäÿßÿ™</h3>
            <ul className="space-y-3 text-sm">
              <ActivityCard
                icon="üí∞"
                text="ÿ™ŸÖ ÿ•ŸÜÿ¥ÿßÿ° ŸÅÿßÿ™Ÿàÿ±ÿ© ÿ®ŸÇŸäŸÖÿ© 245 ÿ±.ÿ≥ ÿ®Ÿàÿßÿ≥ÿ∑ÿ© ÿ£ÿ≠ŸÖÿØ."
              />
              <ActivityCard
                icon="üì¶"
                text="ÿ™ŸÖ ÿ™ÿ≠ÿØŸäÿ´ ŸÖÿÆÿ≤ŸàŸÜ ÿØŸàÿßÿ° ‚ÄúŸÅŸäÿ™ÿßŸÖŸäŸÜ ÿ≥Ÿä‚Äù."
              />
              <ActivityCard
                icon="üìä"
                text="ÿ™ŸÖ ÿπÿ±ÿ∂ ÿ™ŸÇÿ±Ÿäÿ± ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™ ÿßŸÑŸäŸàŸÖŸäÿ©."
              />
            </ul>
          </div>

          {/* ÿßŸÑÿ™ÿ≠ŸÑŸäŸÑ */}
          <div className="p-5 border border-green-300 shadow-lg rounded-xl bg-gradient-to-br from-green-50 to-green-100">
            <h3 className="mb-3 text-lg font-bold text-green-800">üìà ÿ™ÿ≠ŸÑŸäŸÑ ÿßŸÑÿ£ÿØÿßÿ°</h3>
            <p className="text-sm leading-relaxed text-green-700">
              ÿ£ÿØÿßÿ° ÿßŸÑŸÖÿ®Ÿäÿπÿßÿ™ ÿßÿ±ÿ™ŸÅÿπ ÿ®ŸÜÿ≥ÿ®ÿ© <strong>+12%</strong> ÿßŸÑÿ£ÿ≥ÿ®Ÿàÿπ ÿßŸÑŸÖÿßÿ∂Ÿäÿå  
              ŸÖÿπ ÿ≤ŸäÿßÿØÿ© ŸÅŸä ÿπÿØÿØ ÿßŸÑÿ∑ŸÑÿ®ÿßÿ™ <strong>+8%</strong>.  
              ÿßÿ≥ÿ™ŸÖÿ± ÿ®ÿ™ÿ≠ÿ≥ŸäŸÜ ÿßŸÑÿπÿ±Ÿàÿ∂ ŸàÿßŸÑÿ≥ÿ±ÿπÿ© ŸÑÿ≤ŸäÿßÿØÿ© ÿßŸÑÿ£ÿ±ÿ®ÿßÿ≠.
            </p>
          </div>

        </div>
      </div>
    </Layout>
  );
}

// üü° ÿ®ÿ∑ÿßŸÇÿ© ÿßŸÑŸÖŸÑÿÆÿµ
function SummaryCard({ title, value, color }) {
  return (
    <div className="p-5 transition bg-white border shadow-md rounded-xl hover:shadow-lg">
      <p className="text-sm text-gray-500">{title}</p>
      <h3 className={`mt-1 text-2xl font-bold ${color}`}>{value}</h3>
    </div>
  );
}

// üü£ ÿ®ÿ∑ÿßŸÇÿ© ÿπŸÖŸÑŸäÿ©
function ActivityCard({ icon, text }) {
  return (
    <li className="flex items-center gap-3 p-3 transition border rounded-lg bg-gray-50 hover:bg-gray-100">
      <span className="text-xl">{icon}</span>
      <span>{text}</span>
    </li>
  );
}















